#mouth-esophagus-replay-btn {
  width: 140px;
  height: 45px;
  font-family: 'Roboto', sans-serif;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: #000;
  background-color: #fff;
  border: none;
  border-radius: 45px;
  -webkit-box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  position: absolute;
  bottom: -15px;
  }

#mouth-esophagus-replay-btn:hover {
  background-color: #2EE59D;
  -webkit-box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
          box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  -webkit-transform: translateY(-7px);
      -ms-transform: translateY(-7px);
          transform: translateY(-7px);
}

.donut {
    -webkit-transform: scale(.6) translate(100px, 690px);
        -ms-transform: scale(.6) translate(100px, 690px);
            transform: scale(.6) translate(100px, 690px);
    -webkit-animation: donut 4s ease-in-out 1 backwards;
            animation: donut 4s ease-in-out 1 backwards;
    opacity: 0;
}

@-webkit-keyframes donut {
  100% {
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(.6) translate(420px, 690px);
            transform: scale(.6) translate(420px, 690px);
    opacity: 1;
  }
  75% {
    -webkit-transform: scale(.6) translate(575px, 650px);
            transform: scale(.6) translate(575px, 650px);
    opacity: 1;
  }
  80% {
