path.cookie {
    -webkit-animation: cookie 2s ease-in-out infinite;
            animation: cookie 2s ease-in-out infinite;
}

@-webkit-keyframes cookie {
  0%,
  25% {
    -webkit-transform: translate(6px, 6px);
            transform: translate(6px, 6px);
  }
  50%,
  75% {
    -webkit-transform: translate(-9px, -9px);
            transform: translate(-9px, -9px);
  }
  100% {
    -webkit-transform: translate(6px, 6px);
            transform: translate(6px, 6px);
  }
}

@keyframes cookie {
  0%,
  25% {
    -webkit-transform: translate(6px, 6px);
            transform: translate(6px, 6px);
  }
  50%,
  75% {
    -webkit-transform: translate(-9px, -9px);
            transform: translate(-9px, -9px);
  }
  100% {
    -webkit-transform: translate(6px, 6px);
            transform: translate(6px, 6px);
  }
}

circle.pupil {
  -webkit-animation: pupil 2s ease-in-out infinite;
          animation: pupil 2s ease-in-out infinite;
}

@-webkit-keyframes pupil {
  0%,
  25% {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
  50%,
  75% {
    -webkit-transform: translate(-1.5px, -1.5px);
            transform: translate(-1.5px, -1.5px);
  }
  100% {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
}

@keyframes pupil {
  0%,
  25% {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
  50%,
  75% {
    -webkit-transform: translate(-1.5px, -1.5px);
            transform: translate(-1.5px, -1.5px);
  }
  100% {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
}


